body {
  background: $gray-200;
  font-family: $font-family-base;
  font-size: $font-size-base;
  line-height: $line-height-base;
}

a:link,
a:visited {
  &:hover {
    text-decoration: none;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-family: $font-header-base;

  &.display-6 {
    font-family: $font-family-base;
    font-weight: normal;
  }

  &.fw-bold {
    font-family: $font-header-base;
  }
}

strong,
.fw-bold {
  font-family: $font-header-base;
}

ul {
  li {
    margin-bottom: .5rem;
  }
}

.text-white {
  a {
  color: $ltblue;

    &.btn-primary {
      color: $black;
    }
  }

  .fs-3,
  .fs-4 {
    color: $gray-200;
  }
}

footer.bg-black-diagonal {
  a {
    color: $orange;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

.blockquote-footer {
  font-size: 1.25rem;
  color: $white;
  margin-top: 1rem;
}

.py-5 {
  padding-top: 5rem !important;
  padding-bottom: 5rem !important;
}

.bg-black-diagonal {
  background-color: $black;
  opacity: 1;
  background: repeating-linear-gradient( 45deg, $gray-900, $gray-900 2px, $black 2px, $black 10px );
}

.bg-blue-diagonal {
  background-color: $blue;
  opacity: 1;
  background: repeating-linear-gradient( 45deg, $ltblue, $ltblue 2px, $blue 2px, $blue 10px );
}

.bg-x {
  background-color: $gray-100;
  background-image: url('/assets/img/prx-mark-outline-white.svg');
  background-repeat: repeat;
}

.bg-x-blue {
  background-color: $blue;
  background-image: url('/assets/img/prx-mark-outline-blue.svg');
  background-repeat: repeat;

  &.text-white {
    .fs-3,
    .fs-4 {
      color: #fff;
    }
  }
}

.bg-white {
  background-color: $white;

  p, li {
    color: $gray-600;
  }
}

.bg-gray {
  background-color: $gray-200;
}

.bg-cyan {
  background-color: $cyan;
}

.bg-ltblue {
  background: $cyan;
}

.bg-darkblue {
  background-color: $dark-blue;
  opacity: 1;
  background-image:  radial-gradient($blue 1.1500000000000001px, transparent 1.1500000000000001px), radial-gradient($blue 1.1500000000000001px, $dark-blue 1.1500000000000001px);
  background-size: 46px 46px;
  background-position: 0 0,23px 23px;
}

.bg-darkgreen {
  background-color: $dark-green;
}

.bg-polka {
  background-color: $white;
  opacity: 1;
  background-image:  radial-gradient($cyan 1.1500000000000001px, transparent 1.1500000000000001px), radial-gradient($cyan 1.1500000000000001px, $white 1.1500000000000001px);
  background-size: 46px 46px;
  background-position: 0 0,23px 23px;
}

.bg-boxes {
  background-color: $white;
background-image:  linear-gradient($white 1px, transparent 1px), linear-gradient(to right, $white 1px, $gray-100 1px);
background-size: 20px 20px;
}

.bg-wavy {
  background-color: #fff;
  opacity: 1;
  background-image:  repeating-radial-gradient( circle at 0 0, transparent 0, #fff 5px ), repeating-linear-gradient( #e5e5e555, #e5e5e5 );
}

.bg-zig-zag {
  background-color: #eee;
  opacity: 1;
  background-image:  linear-gradient(135deg, #f5f5f5 25%, transparent 25%), linear-gradient(225deg, #f5f5f5 25%, transparent 25%), linear-gradient(45deg, #f5f5f5 25%, transparent 25%), linear-gradient(315deg, #f5f5f5 25%, #eee 25%);
  background-position:  5px 0, 5px 0, 0 0, 0 0;
  background-size: 10px 10px;
  background-repeat: repeat;
}

.section-quote {
  position: relative;
  overflow: hidden;
}

.icon-svg {
  img {
    position: relative;
    z-index: 2;
    max-width: 150px;
    width: 100%;
    height: auto;
  }
}

.hero {
  padding: 0;
  position: relative;
  overflow: hidden;

  &.lede {
      padding-top: 10rem;
  }

  .hero-x-content {
    margin-top: 0;
    position: relative;
    padding-top: 0;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-bottom: 0;
    background: rgb(0,58,87);
    background: linear-gradient(90deg, rgba(0,58,87,1) 0%, rgba(0,114,163,0.5) 50%, rgba(0,114,163,0) 100%);

    .hero-content-inner {
      z-index: 5;
    }
  }

  &.cta {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  &.cta,
  &.hero-no-x {
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgb(0,114,163);
      background: linear-gradient(90deg, rgba(0,114,163,.8) 0%, rgba(0,114,163,0.4) 100%);
    }
  }
}

.hero-image {
  top: 0;
  left: 0;
  width: 100%;
  bottom: 0;
  z-index: 0;
  position: absolute;

  div {
    display: block;
    overflow: hidden;
    position: absolute;
    inset: 0px;
    box-sizing: border-box;
    margin: 0px;
  }

  img {
    position: absolute;
    inset: 0px;
    box-sizing: border-box;
    padding: 0px;
    border: none;
    margin: auto;
    display: block;
    width: 0px;
    height: 0px;
    min-width: 100%;
    max-width: 100%;
    min-height: 100%;
    max-height: 100%;
    object-fit: cover;
  }

  &.post {
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgb(0,114,163);
      background: linear-gradient(90deg, rgba(0,114,163,.8) 0%, rgba(0,114,163,0.4) 100%);
    }
  }
}

.hero-content {
  display: grid;
  grid-gap: 1rem;
  grid-row: 1 / -1;
  position: relative;
  grid-column: 1 / -1;
  align-content: end;

  padding-right: .75rem;
  padding-left: .75rem;

  .hero-content-inner {
    display: flex;
    z-index: 1;
    grid-row: 1 / -1;
    flex-direction: column;
  }
}

.section-features {
  h3 {
    font-family: $font-family-base;
    font-weight: normal;
    font-style: italic;
    margin-bottom: 1rem;
  }

  img {
    width: 100%;
    height: 150px;
    margin-bottom: 1rem;
    object-fit: cover;
    object-position: center;
    border: 1px solid $gray-300;
  }
}

.product-image {
  img {
    width: 100%;
    height: auto;
  }
}

.post-content {
  h2, h3 {
    margin: 2rem 0 1rem;
  }

  img {
    width: 100%;
    height: auto;
    margin-bottom: 1rem;
  }

  .hero-image {
    img {margin-bottom: 0;}
  }

  a {
    font-family: $font-header-base;
    font-weight: bold;
    color: $blue;
  }
}

.blog-thumbnail {
  img {
    object-fit: cover;
    max-width: 250px;
    max-height: 250px;
  }
}
